.model-frame {
    background: var(--bg-primary);
    height: 28rem;
    //width: 65vw;
    width: 56rem;
    display: flex;
    justify-content: center;
    align-items: center;
  
    .image-container {
      width: 65%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .model-info-container {
      width: 35%;
      height: 100%;
      background: white;
      display: flex;
      align-items: center;
  
      .info-skeleton {
        width: 100%;
        height: 90%;
        display: flex;
        justify-content: center;
      }
    }
  }
  
  @media only screen and (max-width: 1000px) {
    .model-frame {
      flex-direction: column;
      width: 56rem * 0.65;
      height: 90vh;
  
      .image-container {
        width: 100%;
        height: 65%;
      }
      .model-info-container {
        width: 100%;
        height: 35%;
        justify-content: flex-end;
  
        .info-skeleton {
          width: 100%;
        }
      }
    }
  }
  
  @media only screen and (max-width: 600px) {
    .model-frame {
      width: 80vw;
      height: 80vh;
  
      .image-container {
        width: 100%;
        height: 50%;
      }
      .model-info-container {
        width: 100%;
        height: 50%;
  
        .info-skeleton {
          width: 100%;
        }
      }
    }
  }