.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 180px;
    height: 80px;
    .my-name {
      font-size: 1.2rem;
      margin-bottom: 5px;
    }
    .a-tags {
      display: flex;
      justify-content: space-around;
      width: 100%;
  
      .icon {
        color: var(--text-primary);
        font-size: 2.1rem;
  
        &:hover {
          color: #43aeae;
        }
      }
    }
  }